export const setAttendStatus = (state,item) =>{
    let newEvent = item;  
    let newEvents = state.events.map((event) => {
        if(event.text === newEvent.text){
           event.attend = true;
           return event;    
        }
        return event;
    });
    console.log(newEvents)
    
    return state.events = newEvents
}
