<template>
         <v-flex xs12 sm6 md3>
              <v-card color="#edf1f7" class="black--text">
                <v-layout row wrap>
                  <v-flex xs12 >                  
                   <h4> {{item.text}}</h4>
                   <v-subheader>   {{ item.date }}         </v-subheader>
                  </v-flex>  
                </v-layout>
                <v-layout row>                
                  <v-flex xs6>
                    <v-card-title primary-title>
                      <div>
                        <div>{{item.place}}</div>                  
                      </div>
                    </v-card-title>
                  </v-flex>
                  <v-flex xs6>
                    <v-img
                      :src="item.image"
                      height="125px"
                      contain
                    ></v-img>
                    
                  </v-flex>
                </v-layout>
                <v-divider light></v-divider>
                <v-card-actions class="pa-3">
                  <div v-if="!item.attend">
                     <v-btn v-if="containsEuro(item.price)" class="primary" @click="buyTicket(item)">Ticket kaufen</v-btn>
                     <v-btn v-if="!containsEuro(item.price)" class="success" @click="attendEvent(item)">Teilnehmen</v-btn>
                  </div>
                  <v-btn v-else class="success" disabled="true">Angemeldet</v-btn>
            
                  <v-spacer></v-spacer>
                  <p>{{item.price}}</p>          
                </v-card-actions>
                <v-snackbar
                  v-model="attend"
                   :top="true">
                Du nimmst am Event "{{item.text}}" teil.
                <v-btn
                    color="pink"
                    flat
                    @click="attend = false"
                >
                    Close
                        </v-btn>
                </v-snackbar>
              </v-card>
            </v-flex>
</template>

<script>
export default {
 data(){
     return {
         attend:false
     }
 },
 props:['item'],
 methods:{
    containsEuro(price){
      if(price != null && price.includes("€"))
        return true;
      else 
        return false;
        
    },
    cutAfter15(text){
        return text.substring(0,15)
    },
    buyTicket(item){
       this.$store.dispatch('Payment/buyTicket', item)
       .then(() => {
          this.$router.push('/checkout')          
       });       
    },
    attendEvent(item){
        this.attend = true;
        this.$store.dispatch('Events/setAttendStatus', item);
         
    }
  }
}
</script>

